				"html": "<p>Welcome to the wiki section of the forum.</p>\n<p>We invite you to use, improve, and create new ones as you see fit. It is a great way to contribute to our community.</p>\n<p>We welcome anything relevant to software development, from tutorials to conceptual overviews to practical advice.</p>\n<p>Anyone with <strong>Trust Level 1</strong> (which you'll get automatically after hanging out reading articles for about an hour) can edit and create wiki post.</p>\n<p>Our moderators will edit, delete, or re-categorize anything that violates our <a href=\"https://www.freecodecamp.com/code-of-conduct\">Code of Conduct.</a> or doesn't belong in the wiki section (such as questions or challenges solutions).</p>\n<h2>How to create a wiki article</h2>\n<ol>\n<li>Create your topic as you normally would</li>\n<li>Click on the post's \"...\" menu, and select \"Make Wiki\"</li>\n<li>Send a private message to <a href=\"/users/rafase282\">@Rafase282</a> to let him know you created an article and ease the transition of the ownership change</li>\n<li>Please avoid writing in the first person, and don't sign the article or promote yourself via links. Don't worry - your authorship will be preserved as part of the article's version history.</li>\n</ol>\n<h2>FAQ</h2>\n<ol>\n<li>\n<p><strong>Why do I need to change ownership of the article to <a href=\"/users/camperbot\">@camperbot</a>?</strong><br>\nIt helps us better track changes to the article, and prevents any vandalism of an article from reflecting poorly on its original author. It also gives everyone a <a href=\"//forum.freecodecamp.com/users/camperbot/activity\">public place where they can track wiki activity</a>.</p>\n</li>\n<li>\n<p><strong>How do I transfer ownership?</strong><br>\n<a href=\"/users/rafase282\">@Rafase282</a> will eventually do this for you, but if you have a moment, <a href=\"//forum.freecodecamp.com/users/rafase282\">send him a message</a> to speed things up.</p>\n</li>\n</ol>",
